.container {
  width: 700px;
  background-color: #fff;
  border-radius: 18px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 3px 3px 20px #000c;
}

h1 {
  margin: 10px;
  font-size: 28px;
}

input {
  width: 90%;
  height: 40px;
  border: 2px solid #000c;
  margin-bottom: 16px;
  padding: 5px 10px;
  font-size: 18px;
}

.btn {
  cursor: pointer;
  background-color: #db33eb;
  border: 2px solid #0006;
  border-radius: 10px;
  margin-top: 5px;
  padding: 10px 30px;
  font-size: 18px;
  font-weight: 500;
  box-shadow: 2px 2px 4px #0009;
}

.error {
  text-align: center;
  width: 70%;
  background-color: #ff8b8b;
  padding: 5px 0;
  display: none;
}

h3 {
  margin-top: 5px;
  font-size: 22px;
}

.message {
  color: #2c1dff;
  margin-top: -5px;
  font-size: 18px;
  font-weight: bold;
}

